body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.full-screen-overlay {
  position: fixed;
  /* Sit on top of the page content */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  /* Black background with opacity */
  z-index: 99;
  /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer;
  /* Add a pointer on hover */
  min-height: 100vh !important;
  min-width: 100vw !important;
  position: fixed;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.margin-top-20 {
  margin-top: 20px;
}

.content-responsive {
  // margin-left: 0 !important;
  // padding-left: 0 !important;
  min-height: 100vh !important;

  // Above Desktop
  @media (min-width: 992px) {
    padding-left: 300px !important;
    padding-top: 50px !important;
  }
}

$primary-color: #001529;

.bg-primary {
  background-color: $primary-color !important;
}

.text-white {
  color: white !important;
}

.margin-menu {
  margin-top: 40px;
  margin-bottom: 20px;
}

.clickable {
  cursor: pointer;
}

.sidebar-menu {
  text-align: center;

  .menu-item {
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: transparent;
    margin-left: 10px;
    margin-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
    border-radius: 5px;
    font-size: 15px;

    &:hover, &.active {
      background: linear-gradient(220deg, #bf0a0a, #3003e4);
      background-size: 400% 400%;

      -webkit-animation: CSSGradientAnimator 30s ease infinite;
      -moz-animation: CSSGradientAnimator 30s ease infinite;
      animation: CSSGradientAnimator 30s ease infinite;
      text-decoration: underline;
    }
  }
}

@-webkit-keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

@-moz-keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

@keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}